// $color-1: #4b5c45;
$color-1: #000000;
$color-2: #e9c10c;
$color-3: #13b241;
$color-4: #8cb27f;
$gray: #c7c7c7;
$content: #000000;
$bg: #ffffff;
$inter: "Inter", sans-serif;
$roboto: "Roboto", sans-serif;
body {
  background: $bg;
  color: $content;
  font-family: $roboto;
  font-size: 20px;
}
.container {
  max-width: 1340px;
  margin: 0 auto;
  padding: 0 20px;
}
.text-center {
  text-align: center;
}
.header {
  background-color: #fff;
  padding: 10px 0;
  &__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav {
    .nav-link {
      margin-left: 30px;
      color: $color-1;
      text-decoration: none;
      font-family: $inter;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 29px;
    }
  }
}

.head {
  img {
    width: 100%;
  }
}
.block-title {
  font-family: $inter;
  font-style: normal;
  font-weight: 600;
  font-size: 44px;
  line-height: 53px;
  color: $color-1;
  margin-bottom: 40px;
}
.about {
  padding: 100px 0 0;
  &__block {
    display: flex;
  }
  &__box {
    align-content: space-between;
    display: flex;
    flex-flow: row wrap;
  }
  img {
    margin-left: 35px;
  }
}
.questions {
  padding: 100px 0 0;
  &__block {
    position: relative;
    img {
      max-width: 735px;
      width: 100%;
    }
  }
  &__box {
    position: absolute;
    padding: 50px 30px;
    right: 0;
    top: 12%;
    max-width: 670px;
    width: 100%;
    background: $bg;
  }
}
.evaluation {
  padding: 100px 0;
  &__block {
    display: flex;
    p {
      max-width: 750px;
      width: 100%;
      margin-bottom: 20px;
    }
  }
  img {
    margin-left: 25px;
  }
}
.reviewers {
  background: $color-4;
  padding: 80px 0 107px;
  &__title {
    color: #ffffff;
  }
  &__block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__box {
    background: #ffffff;
    border: 1px solid #4b5c45;
    box-sizing: border-box;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.24);
    border-radius: 5px;
    max-width: 640px;
    width: 100%;
    padding: 30px 45px 30px;
    display: flex;
    margin: 0 auto;
    margin-bottom: 10px;
    .box__img {
      margin-right: 20px;
    }
    .box__text {
      h3 {
        font-family: $inter;
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
        margin-bottom: 10px;
      }
      p {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 20px;
        color: $content;
      }
    }
  }
}

.footer {
  padding: 100px 0 72px;
  .brand {
    margin: 0 auto;
    text-align: center;
    margin-bottom: 50px;
  }
  .copy {
    display: flex;
    justify-content: center;
    font-family: $inter;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    color: #000000;
    margin-bottom: 20px;
  }
  p {
    font-family: $inter;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: $gray;
  }
  .link__info {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    .nav-link {
      font-family: $inter;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 29px;
      margin: 0 5px;
      color: #13b241;
      text-decoration: none;
    }
  }
}

@media (max-width: 1100px) {
  .about {
    &__box {
      width: 60%;
    }
    img {
      max-width: 40%;
      height: auto;
    }
  }
  .questions {
    padding: 80px 0 0;
  }
  .evaluation {
    padding-top: 80px;
    &__block {
      img {
        width: 40%;
      }
    }
  }
}
@media (max-width: 1000px) {
  .block-title {
    text-align: center;
  }
  .header__nav .nav-link {
    margin-left: 15px;
    font-size: 20px;
    line-height: 24px;
  }
  .about {
    padding: 80px 0 0;
    &__block {
      flex-wrap: wrap;
    }

    &__box {
      width: 100%;
      //   margin: 0 auto;
      order: 2;
      p {
        margin-bottom: 15px;
      }
    }
    img {
      order: 1;
      max-width: 60%;
      height: auto;
      margin: 0 auto;
      margin-bottom: 20px;
    }
  }
  .evaluation {
    &__block {
      flex-wrap: wrap;
      p {
        max-width: 100%;
      }
    }
    img {
      order: 2;
      width: 60%;
      margin: 0 auto;
    }
    &__text {
      order: 1;
    }
  }
}
@media (max-width: 768px) {
  .header__block {
    flex-wrap: wrap;
    .logo {
      margin: 0 auto;
      margin-bottom: 10px;
    }
    .navbar {
      margin: 0 auto;
      .nav-link {
        margin-left: 0;
        margin: 0 15px;
      }
    }
  }
  .questions {
    &__block {
      display: flex;
      flex-wrap: wrap;
      img {
        order: 1;
        margin: 0 auto;
      }
    }
    &__box {
      position: relative;
      order: 2;
    }
  }
}
@media (max-width: 681px) {
  .header__nav {
    display: flex;
    flex-wrap: wrap;
  }
  .header__block .navbar .nav-link {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 10px;
  }
  .about img {
    max-width: 80%;
  }
  .about,
  .questions,
  .evaluation {
    padding-top: 50px;
  }
  .evaluation {
    padding-bottom: 50px;
    &__block {
      img {
        width: 80%;
      }
    }
  }
  .questions__box {
    padding: 30px 0;
  }
  .footer {
    padding: 50px 0px;
    .link__info {
      flex-wrap: wrap;
      align-items: center;
      span {
        display: none;
      }
      .nav-link {
        width: 50%;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 15px;
      }
    }
  }
}
@media (max-width: 550px) {
  .block-title {
    font-size: 30px;
    line-height: 36px;
    margin-bottom: 20px;
  }
  .footer p {
    font-size: 17px;
    line-height: 19px;
  }
  .footer .link__info .nav-link {
    font-size: 20px;
    line-height: 24px;
  }
  .about img {
    max-width: 100%;
  }
  .evaluation__block img {
    width: 100%;
    margin: 0;
  }
  .reviewers__box {
    flex-wrap: wrap;
    .box__img {
      margin: 0 auto;
      margin-bottom: 15px;
    }
    .box__text {
      text-align: center;
    }
  }
  .footer .brand {
    margin-bottom: 20px;
  }
}

//landing page style
.header__product {
  .header__nav .nav-link {
    font-size: 18px;
    line-height: 22px;
  }
}
.head__product {
  background-image: url(../img/head__bg.png);
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
  margin-bottom: 30px;
  .head__bg {
    background: rgba(213, 213, 213, 0.5);
    backdrop-filter: blur(5px);
    padding: 50px 0 40px;
    width: 100%;

    .head__title {
      max-width: 1130px;
      width: 100%;
      margin: 0 auto;
      color: $content;
      font-family: $inter;
      font-style: normal;
      font-weight: 700;
      font-size: 44px;
      line-height: 53px;
      margin-bottom: 45px;
    }
    .head__subtitle {
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: $roboto;
      font-style: normal;
      font-weight: 500;
      font-size: 32px;
      line-height: 38px;
      color: $content;
      img {
        width: auto;
        margin-right: 15px;
      }
    }
  }
}
.text {
  margin-bottom: 40px;
  p {
    font-family: $inter;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: $color-1;
    strong {
      font-weight: 600;
    }
  }
}
.review {
  margin-bottom: 80px;
  &__title {
    font-family: $inter;
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 34px;
    color: $color-1;
    margin-bottom: 8px;
  }
  .disclosure {
    display: flex;
    justify-content: end;
    font-family: $inter;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: $color-1;
    text-decoration: none;
    margin-bottom: 10px;
  }

  &__box {
    position: relative;
    margin-bottom: 20px;
    background: #ffffff;
    padding: 40px 0;
    border: 1px solid $color-4;
    box-sizing: border-box;
    box-shadow: 0px 4px 28px rgba(140, 178, 127, 0.46);
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    .box__img {
      padding: 0 30px;
      max-width: 273px;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      img {
        margin: 0 auto;
        width: 100%;
        margin-bottom: 28px;
      }
      h4 {
        font-family: $roboto;
        font-style: normal;
        font-weight: 400;
        font-size: 30px;
        line-height: 35px;
        color: $gray;
        display: none;
      }
      p {
        font-family: $roboto;
        font-style: normal;
        font-weight: 600;
        font-size: 30px;
        line-height: 35px;
        text-align: center;

        color: #000000;
        display: none;
      }
      .read {
        display: flex;
        justify-content: center;
        margin: 0 auto;
        font-size: 16px;
        line-height: 2rem;
        color: $color-1;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
    .line {
      border: 1px solid #f0f0f0;
    }
    .advantages {
      max-width: 295px;
      width: 100%;
      &__title {
        font-family: $roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;
        color: $color-1;
        margin-bottom: 20px;
      }
      p {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        font-family: $roboto;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
        color: #000000;
        img {
          margin-right: 10px;
        }
      }
      p.top {
        font-weight: 600;
        color: #cdaa09;
      }
    }
    .box__stars {
      max-width: 204px;
      width: 100%;
      text-align: center;
      img {
        width: 100%;
        margin: 0 auto 50px;
      }
    }
    .box__price {
      max-width: 270px;
      width: 100%;
      padding: 20px 20px 20px 10px;
      display: flex;
      justify-content: end;
      flex-direction: column;
      .position {
        position: absolute;
        top: 20px;
        right: -5px;
        background: #e9c10c;
        border-radius: 2px;
        font-family: $roboto;
        font-style: normal;
        font-size: 36px;
        font-weight: 800;
        line-height: 42px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #ffffff;
        padding: 0px 25px;
        span {
          font-size: 20px;
          line-height: 23px;
          margin: 14px 5px;
        }
      }
      h4 {
        font-family: $roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 28px;
        line-height: 33px;
        color: $color-1;
        margin-bottom: 20px;
      }
      .price {
        display: flex;
        margin-bottom: 50px;
        align-items: center;
        .sale {
          font-family: $inter;
          font-style: normal;
          font-weight: 500;
          font-size: 28px;
          line-height: 34px;
          color: $color-4;
          margin-right: 15px;
        }
        .nosale {
          font-family: $inter;
          font-style: normal;
          font-weight: 500;
          font-size: 24px;
          line-height: 29px;
          text-decoration-line: line-through;
          color: #eb5757;
        }
        &__link {
          width: 100%;
          height: 40px;
          font-family: $roboto;
          font-style: normal;
          font-weight: 800;
          font-size: 20px;
          line-height: 23px;
          display: flex;
          align-items: center;
          justify-content: center;
          text-decoration: none;
          color: #ffffff;
          background: #13b241;
          border-radius: 2px;
          &:hover {
            background: $color-2;
          }
        }
      }
    }
  }
}
